.site-list {
  @include list-reset;
  display: flex;
  flex-direction: column;
  background-color: $main-color;
}

.site-list__item a {
  display: block;
  padding-top: 17px;
  padding-bottom: 15px;
  line-height: 32px;
  color: $white;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: 0 1px $black, inset 0 2px rgba(255, 255, 255, 0.15);
}

.site-list__item:first-of-type a {
  box-shadow: 0 1px $black, inset 0 1px $black, inset 0 2px rgba(255, 255, 255, 0.15);
}

.site-list__item a[href] {
  &:hover {
    color: $pink;
  }

  &:active {
    color: $light-pink;
  }
}

@media (min-width: $desktop-width) {
  .site-list {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 621px;
    margin-left: 172px;
    margin-top: 24px;
    background-color: transparent;
  }

  .site-list__item a {
    width: auto;
    padding-top: 15px;
    padding-bottom: 5px;

    @include font-regular;
    font-size: 18px;
    color: $white;
    text-transform: none;
    box-shadow: none;
  }

  .site-list__item--active a {
    border-bottom: solid 2px $white;
  }

  .site-list__item:first-of-type a {
    box-shadow: none;
  }

  .site-list__item a[href]:active {
    color: $white;
    opacity: 0.3;
  }
}
