.fieldset-wrapper {
  width: 320px;
  margin-left: -20px;
  margin-bottom: 32px;
  padding-top: 37px;
  padding-bottom: 47px;
  background-color: $light-grey;

  &--travel-app {
    margin-bottom: 38px;
  }
}

@media (min-width: $tablet-width) {
  .fieldset-wrapper {
    width: 620px;
    margin: 0;
    margin-bottom: 65px;
    padding-bottom: 0;
    background-color: $white;

    &--achievements {
      order: 4;
    }

    &--travel-app {
      order: 2;
    }
  }
}

@media (min-width: $desktop-width) {
  .fieldset-wrapper {
    margin: 0;
    padding: 0;

    &--travel-app {
      width: 431px;
      margin-top: -16px;
    }

    &--achievements {
      width: 936px;
      margin-bottom: 63px;
    }
  }
}
